.place {
  width: 470px;
  height: 200px;
  background-color: rgb(255, 255, 255);
  /* box-shadow: 0px 3px 7.5px rgba(0, 0, 0, 0.1); */
  display: flex;
  margin: 0 0 1.5em 0;
  border-radius: 10px;
}

.place__arrows {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: aqua; */
  width: 37px;
  height: 200px;
}

.place__arrows__up,
.place__arrows__down {
  width: 30px;
  height: auto;
}

.place__image {
  width: 160px;
  height: 180px;
  margin: 10px;
  object-fit: cover;
  border-radius: 5px;
}
