.navbar__logoText {
  font-family: Inter, sans-serif;
  font-size: 1.3em;
  margin: 0.5em auto 0.5em 0.5em;
  color: aliceblue;
  letter-spacing: -0.1em;
  font-weight: 500;
}

.navbar {
  background: linear-gradient(90deg, #00467f 0%, #a5cc82 100%);
  display: flex;
  /* justify-content: center; */
  align-items: center;
  padding: 0.5em;
}

.navbar__logoImage {
  height: 24px;
}

.navbar__addButton {
  width: auto;
  height: 20px;
  margin-left: auto;
  padding: 0 0.5em 0 0;
}

.navbar__logo__container {
  display: flex;
  align-items: center;
  margin-left: auto;
  position: relative;
  left: 15px;
}
