.place__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1em 0 0 0.5em;
}

.place__info__title__section {
  display: flex;
  align-items: flex-start;
}

.place__info__title {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 1.75em;
  font-weight: bold;
  letter-spacing: -0.05em;
  text-align: left;
  padding: 0 0 5px 0;
}

.place__info__description {
  text-align: left;
  margin: 10px 17px 0 2px;
  font-size: 0.9em;
  font-weight: 300;
}

.place__info__type__icon {
  height: 30px;
  width: 30px;
  margin: 2px 5px 0px 3px;
}

.place__info__type {
  text-transform: uppercase;
  color: rgb(98, 94, 94);
  font-family: Inter;
  letter-spacing: 0.2em;
  font-size: 0.8em;
  margin: 0 0 1em 0;
  display: flex;
  align-items: center;
}

.place__info__pin {
  width: auto;
  height: 15px;
  margin: 0 2.5px 0 0;
}

.place__info__tag__section {
  display: flex;
  flex-wrap: wrap;
  padding: 0.5em 0 0 0;
}

.place__info__tag {
  background: #ebf6f6;
  font-size: 12px;
  font-weight: 600;
  line-height: 20px;
  padding: 5px 10px;
  margin: 7px 7px 7px 0;
  border-radius: 3px;
}
